body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

:root {
  --banamania: #ffe9ac;
  --maxYellowRed: #ffc45f;
  --carrotOrange: #f18f01;
  --vermilion: #c73e1d;
  --jet: #2d2d2d;
  --textColor: #686868;
}

.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-intro {
  font-size: large;
}

.loadingDiv {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.loadingSpinner {
  animation: app-loading-spinner infinite 0.5s linear;
  color: #f18f01;
  color: var(--carrotOrange);
  font-size: 50px;
}

@keyframes App-logo-spin {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}

@keyframes app-loading-spinner {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}

:root {
  --banamania: #ffe9ac;
  --maxYellowRed: #ffc45f;
  --carrotOrange: #f18f01;
  --vermilion: #c73e1d;
  --jet: #2d2d2d;
}

.heroCoverSection {
  width: 100%;
  height: 100vh;
  background-image: url(/static/media/honeyHomageCoverImage03.175de3c9.jpg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.heroCoverSectionContainer {
  height: 60%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.honeyHomageLogo {
  width: 300px;
}

.honeyHomageCoverTitle {
  font-family: 'Nerko One', cursive;
  font-size: 70px;
  color: #fff;
}

/* Social Media Links Container */
.socialMediaLinksContainer {
  width: 220px;
  margin: 50px 0px 100px 0px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.socialMediaLinksContainer a {
  text-decoration: none;
  font-size: 30px;
  color: #2d2d2d;
  color: var(--jet);
}

.socialMediaLinksContainer a:hover {
  color: #ffc45f;
  color: var(--maxYellowRed);
}

/* Hero Cover Buttons */
.heroCoverButtons {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  justify-content: space-around;
  height: 100px;
}

.shopButton {
  font-family: 'Montserrat', sans-serif;
  color: #fff;
  margin: 0px 10px 0px 10px;
  border: none;
  width: 200px;
  height: 50px;
  border-radius: 20px;
  cursor: pointer;
  background-color: #2d2d2d;
  background-color: var(--jet);
}

.shopButton:hover {
  background-color: #ffc45f;
  background-color: var(--maxYellowRed);
  color: #2d2d2d;
  color: var(--jet);
}

.shopButton:focus {
  outline: none;
}


.loginButton {
  font-family: 'Montserrat', sans-serif;
  color: #fff;
  margin: 0px 10px 0px 10px;
  border: none;
  width: 200px;
  height: 50px;
  border-radius: 20px;
  cursor: pointer;
  background-color: var(--jet);
}

.loginButton:hover {
  background-color: var(--maxYellowRed);
  color: var(--jet);
}

.loginButton:focus {
  outline: none;
}
